.sendMail {
  position: absolute;
  bottom: 0px;
  right: 50px;
  background-color: #404040;
  width: 40%;
  height: 40%;
  max-width: 500px;
  border-top-left-radius: 10px;
  border-top-right-radius: 10px;
  display: flex;
  flex-direction: column;
  box-shadow: 0px 5px 7px 0px rgba(0, 0, 0, 0.24);
}

.sendMail__header {
  padding: 5px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  color: grey;
}

.sendMail__header > h3 {
  color: whitesmoke;
  font-size: 13px;
}

.sendMail__send{
    background-color: #1976d2 !important;
    text-transform: capitalize !important;
    margin: 15px !important;
}


.sendMail__close{
    cursor: pointer;

}

.sendMail > form {
    display: flex;
    flex: 1;
    flex-direction: column;
}

.sendMail > form > input{
    height: 30px;
    padding: 10px;
    border: none;
    border-top: 1px solid whitesmoke;
    outline: none;
}

.sendMail_message{
    flex: 1;
}


.sendMail__error{
  background-color: #fff;
  color: red;
  padding: 2px;
  text-align: right;
}
