.mail {
  flex: 1;
  background-color: #fff;
}

.mail__tools {
  display: flex;
  justify-content: space-between;
  background-color: white;
  border-bottom: 1px solid whitesmoke;
}

.mail__toolsLeft {
  display: flex;
}

.mail__body {
  display: flex;
  flex-direction: column;
  margin: 30px;
  background-color: white;
  padding:20px;
  height: 100vh;
  box-shadow: 0px 5px 7px 0px rgba(0, 0, 0, 0.24);
}


.mail__bodyHeader{
    align-items: center;
    display: flex;
    height: 40px;
    border-bottom: 1px solid whitesmoke;
    position: relative;
}

.mail__bodyHeader  > p {
    margin-left : 20px;
}


.mail__bodyHeader > h2{
    font-weight: 400;
    margin-right: 20px;
}

.mail__message > p {
    padding: 10px;
    margin-right: 20px; 
    overflow-wrap: break-word;
}

.mail__time{
    position: absolute;
    top: 5px;
    right: 0;
    font-size:12px;
    color: gray;
}