.emailRow {
  display: flex;
  align-items: center;
  height: 50px;
  border-bottom: 1px solid whitesmoke;
  cursor: pointer;
  z-index: 999;
}

.emailRow:hover{
    border-top: 1px solid whitesmoke;
    box-shadow: 0px 4px 4px -2px rgba(0,0,0,0.24);
}

.emailRow__options{
    display: flex;

}

.emailRow_message{
    display: flex;
    flex: .6;
    align-items: center;
    font-size: 13px;
    
}

.emailRow_message > h4{
    width: 400px;
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
    padding-left:5px;
    padding-right:5px;
}

.emailRow__title{
    font-size: 13px;
    flex: 0.3;
}

.emailRow__time{
    font-size: 9px;
    font-weight: bold;
    padding-right: 15px;
}

.emailRow__desc{
    font-weight: 400;
    color: grey;
}